.add-coin-page .title {
  display: flex;
  justify-content: space-between;
  padding: 0.9375rem;
}
.add-coin-page .title .left {
  font-size: 1.125rem;
  font-weight: 400;
  color: #ffffff;
}
.add-coin-page .title .right {
  font-size: 0.875rem;
  font-weight: 400;
  color: #00D2FF;
}
.add-coin-page .select-coin {
  padding: 0.9375rem;
}
.add-coin-page .select-coin .am-list-content {
  color: #666666;
  font-size: 0.75rem;
}
.add-coin-page .select-coin .am-list-extra {
  font-size: 0.75rem;
  color: #fff;
}
.add-coin-page .select-coin .am-list-arrow-horizontal {
  font-size: 0.75rem;
  width: 0.4375rem;
  height: 0.4375rem;
  color: #fff;
}
.add-coin-page .select-coin .am-list-item {
  background: rgba(255, 255, 255, 0);
  position: relative;
}
.add-coin-page .select-coin .am-list-item:after {
  content: " ";
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  height: 1px;
  border-bottom: 1px solid #ffffff;
  color: #ffffff;
  transform-origin: 0 100%;
  transform: scaleY(0.5);
}
.add-coin-page .select-coin .am-list-item input {
  font-size: 0.75rem;
  color: #ffffff;
}
.add-coin-page .select-coin .am-list-item .am-input-extra {
  font-size: 0.75rem;
  font-weight: 600;
  color: #ffffff;
}
.add-coin-page .qr-code {
  min-height: 12.1875rem;
}
.add-coin-page .qr-code div {
  display: flex;
  justify-content: center;
}
.add-coin-page .qr-code .code-title {
  padding-top: 1.65625rem;
  padding-bottom: 0.40625rem;
  font-size: 0.75rem;
  height: 0.9375rem;
  font-weight: 400;
  color: #ffffff;
}
.add-coin-page .qr-code .image {
  width: 14.625rem;
  height: 11.5rem;
}
.add-coin-page .qr-code .image img {
  width: 100%;
  height: 100%;
}
.add-coin-page .qr-code .image svg {
  width: 14.625rem;
  height: 11.5rem;
}
.add-coin-page .qr-code .code {
  padding-top: 0.6875rem;
  font-size: 0.75rem;
  font-weight: 400;
  color: #ffffff;
}
.add-coin-page .footerButton {
  margin-top: 2.5rem;
  margin-bottom: 3.125rem;
  padding-left: 1.5625rem;
  padding-right: 1.5625rem;
}
.add-coin-page .footerButton button {
  width: 100%;
  border-width: 0px!important;
}
.add-coin-page .warn {
  padding: 0.9375rem;
}
.add-coin-page .warn .warn-des {
  font-size: 0.75rem;
  padding-bottom: 0.9375rem;
  font-weight: 400;
  color: #ffffff;
}
.add-coin-page .warn p {
  color: #999999;
  line-height: 1.25rem;
}
.am-picker-popup {
  background-color: #13141C !important;
}
.am-picker-popup .am-picker {
  background: #13141C !important;
}
.am-picker-popup .am-picker-popup-title {
  color: #fff;
}
.am-picker-popup .am-picker-col-mask {
  background-image: linear-gradient(to bottom, 13141C, 13141C), linear-gradient(to top, 13141C, 13141C) !important;
  background-image: #13141C !important;
  color: #fff !important;
}
.am-picker-popup .am-picker-col-item {
  color: #fff !important;
}
.am-picker-col-mask {
  background-image: linear-gradient(to bottom, 13141C, 13141C), linear-gradient(to top, 13141C, 13141C) !important;
  background-image: #13141C !important;
  display: none;
}
.am-picker-col-item {
  color: #fff !important;
}
